@import url(https://fonts.googleapis.com/css?family=Roboto);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.introContainer {
    position: relative;
}

.introImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 30%;
}

.introText {
    position: absolute;
    top: 5%;
    left: 5%;
    color: black;
  }

.introTextHello {
    color: black;
    font-size: 5vw;
  }

.introTextName {
    color: black;
    font-size: 5vw;
  }

.introTextTitle {
  color: black;
  font-size: 5vw;
}

